/* FONDO ANIMADO */

@import url(../var_root.css);


body {
    background: linear-gradient(to right, #2283DB, #2B3ADB, #1F26DB, #3176DB);
}

.square-random {
    position: absolute;
    opacity: 0.3;
    background: linear-gradient(to bottom, #1F26DB, #2283DB);
    animation: rotate 30s infinite linear;
    z-index: -1;
    overflow: hidden;
}

@keyframes rotate {
    100% {
        transform: rotate(360deg);
    }
}